export class Tool{
    title: string;
    img: string;
    strength:number;
    constructor(title: string, img:string,strength:number) {
        this.title = title;
        this.img = img;
        this.strength = strength;

    }

    getStrength() {
        return this.strength;
    }
}

export class Hammer extends Tool{

    constructor() {
        super("hammer",process.env.PUBLIC_URL + '/assets/hammer.avif',6);
    }
}